* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.App {
  text-align: center;
  height: 100%;
}



.colflexwrapper {
  height: 100vh;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.col1 {
  display: flex;
  justify-content: center;
  flex: 1;
}

.rowflexwrapper {
  display: flex;
  width: 100%;
}

.leftsection {
  background-color: grey;
  flex: 1;
}

.mainsection {
  background-color: green;
  flex: 2;
  margin: 5px;
}

.rightsection {
  background-color: purple;
  flex: 1;
}

.bottomsection {
  background-color: aquamarine;
  height: 100px;

}